.form {
    padding-top: 180px;
    min-width: 480px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
}

.link {
    text-decoration: none;
    transition: all 0.3s;
    color: #4C4CFF;
}

.link:hover {
    opacity: 0.7;
    transition: all 0.3s;
}